
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.main-logo{
  height: 20vmin;
  pointer-events: none;
}


* {
  box-sizing:border-box
 }
 body,
 html {
  height:100%;
  color:#0b1b44;
  font-family:Open Sans,sans-serif;
  font-size:14px;
  line-height:1.57142857em;
  -webkit-font-smoothing:antialiased;
  counter-reset:push-step
 }
 body {
  padding:0;
  margin:0;
  background-color:#fff
 }
 p {
  margin-top:0;
  margin-bottom:20px
 }
 .wrapper {
  height:100%
 }
 .container {
  margin-right:auto;
  margin-left:auto
 }
 .container:after,
 .container:before {
  content:" ";
  display:table
 }
 .container:after {
  clear:both
 }
 .section-coming-soon {
  padding-top:240px;
  padding-bottom:35px;
  background-image:url(data:image/png;base64,);
  background-repeat:repeat-x;
  background-position:top;
  text-align:center;
  z-index:1
 }
 .section-coming-soon:before {
  content:"";
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:#fff;
  background:linear-gradient(0deg,#fff,#e9e9e9);
  z-index:-1
 }
 .section-coming-soon .logo {
  margin-top: 50px; 
  margin-bottom:50px
 }
 .section-coming-soon .coming-soon {
  margin-bottom:65px
 }
 .section-coming-soon .copyright {
  font-size:13px;
  font-weight:600
 }
 .section-coming-soon .copyright strong {
  text-transform:uppercase
 }
 @media screen and (max-width:767px) {
  .section-coming-soon {
   background-size:50px;
   padding-top:100px;
   padding-bottom:20px
  }
  .section-coming-soon .logo {
   margin-bottom:30px;
  }
  .section-coming-soon .logo img {
   width:200px
  }
  .section-coming-soon .coming-soon {
   margin-bottom:40px
  }
  .section-coming-soon .coming-soon img {
   width:185px
  }
  .section-coming-soon .copyright {
   font-size:13px;
   font-weight:600
  }
 }
 
